@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900');

@tailwind base;
@tailwind components;
@tailwind utilities;

.custom-bg {
  background: radial-gradient(125% 125% at 50% 10%, #767676 40%, #63e 100%);
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Poppins';
  margin: 0;
  padding: 0;
  outline: none;
}

::-webkit-scrollbar{
width: 20px;
}
::-webkit-scrollbar-track{
background: rgba(0, 0, 0, 0.9);
}
::-webkit-scrollbar-thumb{
background: linear-gradient(to bottom, rgb(0, 62, 196), rgb(184, 0, 153));
}
html {
    font-size: 16px; /* Taille de police fixe */
  }

body {
    margin: 0;
    z-index: -200;
    overflow-x: hidden ;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    background-image: url('../img/fondBleu.png');
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
  }

  ul{
    list-style: none;
    padding: 0;
    margin:0 ;
  }

  a{
    text-decoration: none;
  }
  

.main{
    min-height: 200vh;
    padding: 6rem 1rem 1rem;
    h1, p{
        margin-bottom: 1rem;
    }
}